#Prevention{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
}


.preventionCard{
    margin: 50px 20px;
    padding: 40px;
    width: 400px;
    border: 1px solid #b89d55;
    border-radius: 5px;
    box-shadow: 4px 9px 5px -2px rgba(0,0,0,0.61);
}

ul{
    padding-left: 5px;
    color: white;
}

li{
    padding: 10px 0;
}

.preventionCard div{
    color: white;
    font-weight: 300;
}

.preventionCardHeadline{
    border-bottom: 1px solid white;
    font-size: 25px;
    font-weight: 500;
}

.preventionStep{
    padding-bottom: 10px;
}